* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("https://res.cloudinary.com/drdqfuc0t/image/upload/v1727443089/News_fevv5n.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
  margin: 0;
}

.obj-width {
  max-width: 1200px;
  margin: auto;
}

nav {
  z-index: 1000;
  background-color: #5c4735;
  position: sticky;
  top: 0;
  box-shadow: 0 7px 10px #0000004d;
}

.navbar {
  justify-content: space-around;
  align-items: center;
  padding: 1.3rem 0;
  display: flex;
}

nav ul {
  justify-content: space-around;
  align-items: center;
  font-size: 1.5rem;
  list-style: none;
  display: flex;
}

nav ul li {
  cursor: pointer;
}

nav ul li:hover {
  color: #fff;
  text-decoration: underline;
}

.navbar .logo {
  color: #fff;
  letter-spacing: 2px;
  font-size: 2.2rem;
  text-decoration: none;
}

#search-input {
  color: #333;
  border: none;
  border-radius: 25px;
  max-width: 400px;
  padding: 10px 20px;
  font-size: 18px;
  transition: all .3s ease-in-out;
  box-shadow: 0 4px 6px #0000001a;
}

#search-button {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 25px;
  margin-left: 10px;
  padding: 10px 25px;
  font-size: 18px;
  transition: all .3s ease-in-out;
  box-shadow: 0 4px 6px #0000001a;
}

#search-button:hover {
  background-color: #e4b58d;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px #0003;
}

#search-input:focus {
  outline: none;
  box-shadow: 0 6px 12px #0003;
}

#bolg-container {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 3rem 0;
  display: flex;
}

.bolg-card {
  cursor: pointer;
  background-color: #f9f4ef;
  border: 1px solid #2d4c50;
  border-radius: 10px;
  width: 280px;
  margin: 10px;
  padding: 1rem;
  transition: all .3s ease-in-out;
  box-shadow: 4px 4px 10px #0000001a;
}

.bolg-card:hover {
  transform: translateY(-5px);
  box-shadow: 6px 6px 15px #0003;
}

.bolg-card img {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 280px;
  margin-bottom: 1rem;
}

.bolg-card h2 {
  color: #141414;
  font-size: 1.7rem;
  font-weight: 500;
}

.bolg-card p {
  padding-top: .7rem;
  font-size: 1.2rem;
}

footer {
  text-align: center;
  color: #fff;
  background-color: #5c4735;
  width: 100%;
  padding: 20px 0;
  font-size: 1rem;
  position: relative;
  bottom: 0;
}

footer a {
  text-decoration: none;
}

footer img {
  width: 150px;
  margin-bottom: 10px;
}

footer p {
  color: #fff;
  margin-top: 10px;
  font-size: .9rem;
}

footer a img:hover {
  opacity: .8;
}

footer p {
  font-style: italic;
}

@media (width <= 500px) {
  nav {
    padding: 0 20px;
  }

  .navbar .logo {
    font-size: 1.8rem;
  }

  #search-input {
    width: 150px;
    padding: 10px;
  }

  .bolg-card {
    width: 100%;
  }
}
